export default "/**\n *\n * Welcome to my website! As an HR Process Engineer, I specialize in streamlining HR operations to reduce costs, \n * improve efficiency, and enhance the overall employee experience. By applying process improvement methodologies \n * such as Lean, Six Sigma, and Agile, I optimize HR functions such as recruitment, onboarding, performance management,\n * and employee relations. I also provide consulting services to help organizations optimize their business  processes.\n * \n * As a hobby, I enjoy full stack web development, and have experience in various programming languages.\n *\n * The main purpose of this website is to showcase my skills and experiences in a fun and interactive way.\n * While demonstrating how HTML, Markdown, and CSS work together, I hope you enjoy the experience.\n *\n *\n * What's a better way to get to know me than to see my work?\n *  \n *    **** How about a little live coding? ****\n *\n *\n *\n *\n * You can skip everything by clicking skip animation in the bottom left corner.\n * \n * \n * Let's begin. We start by animating... well, everything.\n*/\n\n* {\n -webkit-transition: all 1s;\n}\n\n\n\n/**\n* That didn't do much. But you'll see.\n*\n* Black on white is really boring,\n* so let's do something about it.\n*/\n\nhtml {\n background: rgb(63, 82, 99);\n}\n\n/***\n* Hold on...\n*/\n\npre, a {\n color: white;\n}\n\n/**\n* That's better. Sorry about your eyes.\n*\n* Working in this big empty space is tough.\n*\n* I'm going to make a nice area for us to work in.\n*/\n\npre:not(:empty) {\n overflow: auto;\n background: rgb(31, 31, 31);\n border: 1px solid #ccc;\n max-height: 45vh;\n width: 49%;\n font-size: 14px;\n font-family: monospace;\n padding: 1vh 0.5vw;\n box-shadow: -4px 4px 2px 0 rgba(0,0,0,0.3);\n white-space: pre-wrap;\n outline: 0;\n margin: 1vh 0.5vw;\n}\n\n/**\n* Okay. We're going to start filling up the screen.\n* Let's get ready to do some work.\n*/\n\n#style-text {\n -webkit-transform: translateX(95%);\n position: absolute;\n}\n\n/**\n* This is good, but all the text is white!\n* Let's make it even more readable.\n*/\n\n.comment       { color: #857F6B; font-style: italic; }\n.selector      { color: #E69F0F; }\n.selector .key { color: #64D5EA; }\n.key           { color: #64D5EA; }\n.value         { color: #BE84F2; }\n.value.px      { color: #F92772; }\n\n/**\n* Now we're getting somewhere.\n* Time to get a little perspective.\n*/\n\nbody {\n -webkit-perspective: 1000px;\n}\n\n#style-text {\n -webkit-transform: translateX(98.5%) rotateY(-10deg);\n -webkit-transform-origin: right;\n max-height: 93.1vh;\n}\n\n/**\n* What is Markdown? Its a light-weight markup language. It's easy to read and write,\n* and can be converted to HTML. It's a great way to write content for the web.\n*/\n\npre:not(#style-text) {\n -webkit-transform: rotateY(10deg);\n wekkbit-transform-origin: left;\n \n}\n";